body, html {
  min-height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  background-color: white;
  color: black;
  font-family: 'Montserrat', sans-serif;
  -webkit-transition: background .5s ease;
  -o-transition: background .5s ease;
  transition: background .5s ease;
}

h1{
  font-weight: 0;
  -webkit-transform: uppercase;
      -ms-transform: uppercase;
          transform: uppercase;
}

a {
  color: inherit !important;
  text-decoration: none !important;
}

p, li {
  font-size: 1.25em;
}

.break {
  width: 10%;
  margin-top: 3%;
  margin-bottom: 3%;
  font-size: 0.1em;
}

.btn {
  margin-top: 2%;
  -webkit-transition: background .25s;
  -o-transition: background .25s;
  transition: background .25s;
  background-color: transparent;
}

.btn:hover {
  -webkit-transition: background .25s;
  -o-transition: background .25s;
  transition: background .25s;
}

/* Nav bar */
.navbar img {
  height: 2.75em;
  margin-top: -25% !important;
}

.navbar a {
  color: #5e507e !important;
}

/* Hero */

.hero {
  height: 100vh;
  position: relative;
  overflow: hidden;
  text-align: left;
}

.hero-cont {
  padding-left: 5%;
}

.hero-text {
  text-align: left;
  position: absolute;
  width: 50%;
  bottom: 5%;
  color: #404041;
}

@media (max-width: 1024px) {
  .hero-text {
    width: 90%;
  }
}

@media (min-width: 1024px) {
  .hero-text {
    width: 50%;
  }
}

.hero h1 {
  color: #5e507e;
  font-size: 4.5em;
}

.hero .break {
  background-color: #5e507e;
}

.hero .btn {
  border: 1px solid #5e507e;
  border-radius: 100px;
  color: #5e507e;
}

.hero .btn:hover {
  background-color: #5e507e;
  color: white;
}

/* Projects */
.project {
  height: 100vh;
  position: relative;
  overflow: hidden;
  text-align: left;
  color: white;
  -webkit-transform:rotate(60);
      -ms-transform:rotate(60);
          transform:rotate(60);
}

.project .break {
  background-color: white;
}

.project .btn {
  border: 1px solid white;
  border-radius: 100px;
  color: white;
}

.project .btn:hover {
  background-color: white;
  border-color: transparent;
  color: #5e507e;
}

.project .row {
  -webkit-transform: translateY(50%);
      -ms-transform: translateY(50%);
          transform: translateY(50%);
}

.project img {
  position: absolute;
  border-radius: 2%;
  z-index: -1;
  visibility: hidden;
}

.animated {
  visibility: visible !important;
}

.project #img1 {
  -webkit-transform: translate(-50%, -40%);
      -ms-transform: translate(-50%, -40%);
          transform: translate(-50%, -40%);
  max-width: 65%;
}

.project #top-img {
  z-index: 1;
  max-width: 75%;
  margin-top: 25%;
}

.project #img2 {
  -webkit-transform: translate(-50%, -15%);
      -ms-transform: translate(-50%, -15%);
          transform: translate(-50%, -15%);
  max-width: 40%;
  margin-top: 50%;
  margin-right: 25%;
}

/* Contact */
.contact {
  height: 100vh;
  position: relative;
  overflow: hidden;
}
.contact-text {
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0 5%;
  text-align: center;
  position: absolute;
  color: white;
  top: 50%;
  left: 50%;
}

.contact .break {
  background-color: white;
  margin: auto;
  margin-top: 20em;
  margin-bottom: 20em;
}

.contact .btn {
  border: 1px solid white;
  border-radius: 100px;
  color: white;
}

.contact .btn:hover {
  background-color: white;
  color: #6dc1dd;
}

.contact input {
  border-radius: 10px;
  border-color: white;
  background-color: transparent;
  text-align: center;
  width: 50%;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
  height: 3em;
  color: white;
}

.contact input::-webkit-input-placeholder {
  color: white;
  opacity: 0.75;
}

.contact input:-ms-input-placeholder {
  color: white;
  opacity: 0.75;
}

.contact input::-ms-input-placeholder {
  color: white;
  opacity: 0.75;
}

.contact input::placeholder {
  color: white;
  opacity: 0.75;
}

.contact input:focus {
  border-radius: 10px;
  border-color: white;
  background-color: transparent;
}

/* Footer */
.footer {
  background-color: white;
  text-align: center;
}

.footer .container-fluid{
  margin-left: 2%;
  margin-right: 2%;
}

.leftSide {
  text-align: left;
}

.rightSide {
  text-align: right;
}

.logo{
  max-width: 20%;
}



